.menu {
    position: absolute;
    z-index: 1000;
    width: 100%;
    font-family: Helvetica, sans-serif;
    font-size: 1.5em;
}

.menu button {
    outline: none;
    z-index: 10;
}

.menu-btn {
    width: 40px;
    height: 40px;
    border: 0;
    background: transparent;
    margin: 40px;
    position: relative;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

#menu-svg {
    position: absolute;
    top: 0;
    left: 0;
}

.menu-btn:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.menu-btn:active #menu3 {
    /*transform: translate(0px,22px);*/
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.menu-btn:active #menu2 {
    /*transform: translate(0px,22px);*/
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.menu-links {
    width: calc(50% - 70px);
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    padding-left: 70px;
    background: #797979;
    min-width: 450px;
    -webkit-animation: menu .4s ease-in;
            animation: menu .4s ease-in;
}

@-webkit-keyframes menu {
    from {
        -webkit-transform: translateY(-100vh);
                transform: translateY(-100vh)
    }
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0)
    }
}

@keyframes menu {
    from {
        -webkit-transform: translateY(-100vh);
                transform: translateY(-100vh)
    }
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0)
    }
}

.menu-item {
    padding: 5px;
    color: #c3c3c3;
    cursor: pointer;
}

.menu-item:hover {
    background: #5f5f5f;
    color: #eaeaea;
}

.menu-item:first-of-type {
    margin-bottom: 5px;
    border-bottom: 1px solid #949494;
}

.menu-img {
    width: 50%;
    background: #a7a5a5;
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-animation: menu .6s ease-in;
            animation: menu .6s ease-in;
}

.menu-img img {
    width: 200px;
    -webkit-filter: drop-shadow(1px 10px 20px rgba(0, 0, 0, 0.63));
            filter: drop-shadow(1px 10px 20px rgba(0, 0, 0, 0.63));
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}

.select {
    padding-left: 30px;
    color: #525252;
}


